import { FlightSearchService } from './../../../../shared/services/flight-search.service';
import { Component, OnInit, Input } from '@angular/core';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-fares-baggage',
  templateUrl: './fares-baggage.component.html',
  styleUrls: [
    './../dynamic-search-results/dynamic-search-results.component.scss',
    './fares-baggage.component.scss']
})
export class FaresBaggageComponent implements OnInit {
  @Input('fareBaggageArr') fareBaggageArr;
  tabType: string = 'fare';
  fareRules: any = null;
  fareRulesLoader: boolean = false;

  constructor(
    private flightSearchService: FlightSearchService,
    private toastr: ToastrService) { }

  ngOnInit() {
    console.log(this.fareBaggageArr);
  }

  checkArray(arr) {
    if (Array.isArray(arr)) {
      return arr;
    } else {
      return [arr];
    }
  }

  activateTab(type) {
    switch (type) {
      case 'fare':
        this.tabType = type;
        break;
      case 'baggage':
        this.tabType = type;
        break;
      case 'rules':
        this.tabType = type;
        break;
    }
  }

  fetchFareRules(fare: any) {
    let rulesObj: any = {};
    this.fareRulesLoader = true;
    console.log(fare)
    fare.echoToken = localStorage.getItem('_ecTk');
    console.log("FARE", fare)
    this.flightSearchService.fareRulesRequest(fare)
      .subscribe((rules: any) => {
        console.log(rules);
        if (rules) {
          this.fareRules = rules.fareRules;
          this.fareRulesLoader = false;
        } else {
          this.toastr.info('Oops! We\'re unable to find Fare Rules for current Flight Segment.', 'No Fare Rules', {
            timeOut: 10000,
            progressBar: false,

            positionClass: 'toast-bottom-right'
          });
        }
      }, (err: any) => {
        this.toastr.info('Oops! We\'re unable to find Fare Rules for current Flight Segment.', 'No Fare Rules', {
          timeOut: 10000,
          progressBar: false,

          positionClass: 'toast-bottom-right'
        });
      })
  }
}
