  updateAncillary(anc: boolean) {
    this.screenTitle = "Passengers";
    this.displayAncillary = anc;
    this.flightsContainer = false;
  }

  setSearchResult(searchResult: boolean) {
    this.displayAncillary = false;
    this.searchResult = !searchResult;
    this.flightsContainer = searchResult;
    this.loadConfig();
  }

  setBookScreen(bookScreen: boolean) {
    this.screenTitle = "Passengers";
    this.displayBookScreen = bookScreen;
    this.flightsContainer = false;
  }

  activeOption(opt) {
    this.optionStatus = false;
    switch (opt) {
      case 'price-option':
        this.optionStatus = !this.optionStatus;
        this.optName = opt;
        break;
      case 'fareQuote-option':
        this.optionStatus = !this.optionStatus;
        this.optName = opt;
        break;
      case 'travel-option':
        this.optionStatus = !this.optionStatus;
        this.optName = opt;
        break;
      case 'fareSel-option':
        this.optionStatus = !this.optionStatus;
        this.optName = opt;
        break;
      case 'qual-option':
        this.optionStatus = !this.optionStatus;
        this.optName = opt;
        break;
      case 'tax-option':
        this.optionStatus = !this.optionStatus;
        this.optName = opt;
        break;
      case 'service-option':
        this.optionStatus = !this.optionStatus;
        this.optName = opt;
        break;
    }
  }

  closeOption(opt) {
    switch (opt) {
      case 'price-option':
        this.optionStatus = false;
        this.optName = opt;
        break;
      case 'fareQuote-option':
        this.optionStatus = false;
        this.optName = opt;
        break;
      case 'travel-option':
        this.optionStatus = false;
        this.optName = opt;
        break;
      case 'fareSel-option':
        this.optionStatus = false;
        this.optName = opt;
        break;
      case 'qual-option':
        this.optionStatus = false;
        this.optName = opt;
        break;
      case 'tax-option':
        this.optionStatus = false;
        this.optName = opt;
        break;
      case 'service-option':
        this.optionStatus = false;
        this.optName = opt;
        break;
    }
  }

