  loadCarousel() {
    var scope = this;
    $(document).ready(function () {
      $('.owl-carousel').owlCarousel({
        loop: true,
        margin: 10,
        autoplay: false,
        nav: true,
        dots: false,
        navText: ['<img src="./assets/img/back.png" />', '<img src="./assets/img/next.png" />'],
        responsive: {
          0: {
            items: 1
          },
          600: {
            items: 3
          },
          1000: {
            items: 5
          }
        }
      });
      $('.owl-carousel').owlCarousel('refresh');
    });
  }

